<template>
    <div class="goods-description">
				<!--返回按钮-->
				<div class="header" >
					<div class="back" v-on:click="back">
						<img class="back-img" src="../../assets/back.png" >
						<span class="back-txt" >返回</span>
					</div>
				</div>
        <div class="goods">
            <img :src="goodsData.image" alt="">
            <div class="description">
                <span>价格：￥{{goodsData.pirce}} <del style="color:#aaa;">￥{{markingPrice}}</del></span>
                <span>已选：{{selectData.attr.indexOf('undefined')>-1?'':selectData.attr}}</span>
            </div>
        </div>
        <div class="color-size">
            <div class="color" v-if="goodsData.productAttr" v-for="(item,index) in goodsData.productAttr" :key="index">
                <span>{{item.attr_name}}</span>
                <ul>
                    <li @click="select(attr,index)" :class="selectAttr[index]==attr?'activa':''" v-for="(attr,value_key) in item.attr_values" :key="value_key">{{attr}}</li>
                </ul>
            </div>
            <div class="counts">
                <p>
                    <span>数量</span>
                </p>
                <div class="count-data">
                    <span class="minus" @click="minus">-</span>
                    <span class="number">{{selectData.num}}</span>
                    <span class="add" @click="add">+</span>
                </div>
            </div>
        </div>

        <!-- 手机号等信息 -->
      <div class="field">
					<mt-field class="mint-field-core" placeholder="收货人姓名" v-model="userInfo.username"></mt-field>
					<mt-field class="mint-field-core" placeholder="手机号码" type="number" v-model="userInfo.tel"></mt-field>
					<mt-field class="mint-field-core" placeholder="收货地址" type="textarea" rows="4" v-model="userInfo.introduction"></mt-field>
			</div>
        <!-- 按钮 -->
        <div @click="toPayment">
        <buybtn-x></buybtn-x>
       </div>

    </div>
</template>
<script>
import userinfo from "../../components/userinfo";
import buybtn from "../../components/buybtn";
import axios from "axios";
export default {
  components: {
    userinfoX: userinfo,
    buybtnX: buybtn
  },
  data() {
    return {
      goodsData: "", //数据源
      markingPrice: 0, //划线价
      selectAttr: [], //选择的规格
      productId: "", //商品id

      userInfo: {
        username: "",
        tel: "",
        introduction: ""
      },
      selectData: {
        num: 1,
        attr: "",
        // price: "",
        unique: ""
      }
    };
  },
  created() {
    this.getInfo();
  },
  methods: {
    back(){
        this.$router.go(-1);//返回上一层
    },

    getInfo() {
      let productId = this.$route.query.productid;
      this.productId = productId;
      axios
        .get(
          "http://app.taijidjk69.com/api/h5/good/ProductAttrDetail?productId=" +
            parseInt(productId)
        )
        .then(response => {
          this.goodsData = response.data.data;
        })
        .catch(error => {
          console.log(error);
          this.$toast({
            positon: "middle"
          });
        });
    },

    toPayment() {
      console.log(this.goodsData);
      if (
        this.userInfo.username.length == 0 ||
        this.userInfo.introduction.length == 0 ||
        this.selectAttr.length !== this.goodsData.productAttr.length
      ) {
        this.$toast({
          message: "亲亲还有未选择的哦~",
          positon: "middle"
        });
        return false;
      }
      if (!/^1[34578]\d{9}$/.test(this.userInfo.tel)) {
        this.$toast({
          message: "亲亲手机号码不正确哦~",
          positon: "middle"
        });
        return false;
      }
      // let unique = this.goodsData.productValue.unique;
      // this.unique = unique;
      // this;
      let query = {
        cartNum: this.selectData.num,
        productId: this.productId,
        // this.selectData.unique
        // "3df5df21"
        // uniqueId: "8d5d4fa1",
        uniqueId: this.goodsData.unique,
        payType: "weixin",
        name: this.userInfo.username,
        phone: this.userInfo.tel,
        address: this.userInfo.introduction
      };
      console.log(this.productId);
      axios
        .get("http://app.taijidjk69.com/api/h5/good/pay", { params: query })
        .then(response => {
          if (response.data.status === 1) {
            let wxpay = {
              order_id: response.data.data.order_id,
              ProductName: response.data.data.ProductName,
              price: response.data.data.price
            };
            axios
              .get("http://app.taijidjk69.com/api/h5/good/wxpayurl", {
                params: wxpay
              })
              .then(res => {
                window.location.href = res.data.mweb_url;
                console.log("获取到支付url");
                // console.log(res);
              })
              .catch(error => {
                console.log(error);
                this.$toast({
                  message: "哎呀~开了点小差",
                  positon: "middle"
                });
              });
          }
          // console.log(response);
        })
        .catch(error => {
          console.log(error);
          alert("好嗨呀......");
        });
    },

    select(item, key) {
      console.log(item);
      // key是循环的次数，item是选择规格的名称
      this.selectAttr[key] = item;
			console.log('selectAttr:'+this.selectAttr)
      // console.log(this.selectAttr)  this.selectAttr是为了记录客户选择哪些规格   每次点击规格的时候   把规格名称压入这个变量里面
      // 如果循环两次   就可以得到this.selectAttr[0] = 白色， this.selectAttr[1] = 3XL的规格值了

      // 如何确定客户已经选择完了所有规格    只要this.selectAttr长度等于渲染出来的规格数   那就是已经选择完了  this.selectAttr.length == this.goodsData.productAttr.length
      if (this.selectAttr.length == this.goodsData.productAttr.length) {
					this.setPrice(); //设置价格
      } else {
        this.getInfo();
      }
    },
    setPrice() {
      //规格选择完毕设置价格
      let key = "";
      //keys用于查找unique属性
			let keys = "";
			//拼接key值
      for (var i = 0, len = this.selectAttr.length; i < len; i++) {
					key += this.selectAttr[i] + ",";
      }
      key = key.substring(0, key.length - 1);
			this.selectData.attr = key;
			//拼接keys值
			for (var y = this.selectAttr.length-1 ; y >=0 ; y--) {
			  if(y!=0){
					keys += this.selectAttr[y] + ",";
			  }else{
			  	keys += this.selectAttr[y] ;
			  }
			}
			console.log(keys);
      // this.goodsData.pirce = this.goodsData.productValue[key].price;
      // this.markingPrice = this.goodsData.productValue[key].cost;
      // this.goodsData.image = this.goodsData.productValue[key].image;
      // this.goodsData.unique = this.goodsData.productValue[key].unique;
			
			this.goodsData.pirce = this.goodsData.productValue[keys].price;
			this.markingPrice = this.goodsData.productValue[keys].cost;
			this.goodsData.image = this.goodsData.productValue[keys].image;
			this.goodsData.unique = this.goodsData.productValue[keys].unique;
			// console.log(this.goodsData.unique)
    },
    minus() {
      if (this.selectData.num > 1) {
        this.selectData.num--;
      }
    },
    add() {
      this.selectData.num++;
    },
		
  }
};
</script>
<style lang="scss" scoped>
.goods-description {
  padding: 0;
}
.header{
	  display: flex;
		padding: 0.9375rem 0.625rem;
		
		.back{
			display: inherit;
			.back-txt{
				font-size:0.88rem;
				color:rgba(127,126,126,1);
			}
			.back-img{
				margin-right: 0.3rem;
				width: 0.6rem;
			}
		}
		
}
	

.goods {
	margin: 0 0.625rem;
  display: flex;
  margin-bottom: 1.4rem;
  img {
    width: 5.56rem;
    height: 5.56rem;
    border: 0.125rem solid #ebebeb;
  }
  .description {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-left: 0.7rem;
		font-size: 0.88rem;
  }
  .description span:first-child {
    color: rgba(255, 130, 39, 1);
    line-height: 1.3rem;
  }
  .description span:last-child {
    color: #4c4c4c;
  }
}
.color-size {
	margin: 0 0.625rem;
  span {
    // font-size: 13px;
		font-size: 0.88rem;
    color: #4c4c4c;
    // margin-top: 0.3rem;
    // border:#ff8227 1px solid;
  }
  .color {
    // display: flex;
    // flex-flow: wrap;
    // height: 6rem;
    margin-bottom: 0.5rem;
    // border: #ff8227 1px solid;
    ul {
      margin: 0.5625rem 0 0 0;
      // width: 95%;
      padding: 0;
      // display: inline-block;
      // height: 5rem;
      // border: red 1px solid;
      li {
        list-style-type: none;
        border: 0.5px solid #aaa;
        border-radius: 0.2rem;
        color: #aaa;
        padding: 0.5rem;
        margin: 0 0.75rem 0.5625rem 0;
        font-size: 0.75rem;
        display: inline-block;
        // border: #000 1px solid;
      }
      .activa {
        color: #ff8227;
        border: 1px solid #ff8227;
      }
    }
  }
  //   .taglia span {
  //   }
  .counts {
		margin-bottom: 0.5rem;
		p{
			margin-top: 0;
		}
    // margin: -2.6rem 0 0.3rem 0;
    // border-bottom: 6px solid #ebebeb;
    .count-data {
      display: flex;
      margin-bottom: 0.5rem;
      margin-top: -0.5rem;
      .minus,
      .add,
      .number {
        border: 1px solid rgba(170, 170, 170, 1);
        text-align: center;
        // border-radius: 0.12rem;
        color: #aaa;
				width:2.72rem;
				height:2rem;
				line-height: 2rem;
				font-size: 1.2rem;
      }
      .minus {
        // border: 1px solid rgba(210, 210, 210, 1);
				border-radius: 0.12rem 0 0 0.12rem; 
				-webkit-border-radius: 0.2rem 0 0 0.2rem; 
				border-right: 0;
      }
      .number {
				font-size: 0.75rem;
        width:3.78rem;
      }
			.add {
			  // border: 1px solid rgba(210, 210, 210, 1);
				border-radius:0 0.2rem 0.2rem 0; 
				-webkit-border-radius: 0 0.2rem 0.2rem 0; 
				border-left: 0;
			}
    }
  }
  .mint-field-core {
    // font-size: 13px !important;
		font-size: 0.88rem;
    // border-bottom:  1px solid #ebebeb;
     // font-family:SourceHanSansCN-Regular;
  }
	
  .field /deep/{
		// border-bottom: 0.2rem solid #ebebeb;
    margin-bottom: 2.3rem;
    color: rgba(182, 182, 182, 1);
    font-size: 14px !important;
    font-family:SourceHanSansCN-Regular;
		
  }
  // .field /deep/ input {
  //   margin-top: -0.6rem;
  //   font-size: 14px;
  //   font-family:SourceHanSansCN-Regular;
  //     border-bottom: 1px solid rgba(235, 235, 235, 1);
  // }

  // .field /deep/ textarea {
  //   margin-top: -0.1rem;
  //   height: 3.1rem;
  //   font-size: 14px;
  //   font-family:SourceHanSansCN-Regular;
  //     border-bottom: 1px solid rgba(235, 235, 235, 1);
  //   overflow: hidden;
  // }
	
}
</style>
<style>
	.field {
		border-top: 0.625rem solid #ebebeb;
	  font-size: 0.88rem;
	}
	.mint-field-core{
		color: #4c4c4c;
		font-size: 0.88rem !important;
	}
	.field .mint-cell-wrapper{
		border-bottom: 1px solid #ebebeb;
	}
	.field input{
		resize: none;
		color: #4c4c4c;
		font-family: inherit;
		height: 2.5rem;
	}
    
	.field textarea{
		resize: none;
		color: #4c4c4c;
		font-family: inherit;
	}

	.color-size .color ul li:last-child{
		margin-right: 0;
	}
</style>


